<template>
    <yd-layout>
        <yd-navbar bgcolor="#70b6ef" title="分享" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">返回</yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="schedules">
            <div class="search">
                <input type="text" v-model="name" name="searchWord" class="searchWord"  @focus="searchFlag = true; orgQueryList= []" @keyup.enter="search(name)" placeholder="搜索">
            </div>
            <div class="searchContent" @click="searchFlag = false; name=''" v-if="searchFlag">
                <ul>
                    <li @click.stop="shareFun(item.id,$event)" class="searchLi" v-for="item in orgQueryList" v-text='item.text+" "+jsonData[jsonData[item.parent].parent].text+"/"+jsonData[item.parent].text'></li>
                </ul>
            </div>
            <yd-cell-group>
                <yd-cell-item arrow @click.native="jiekouFlag = !jiekouFlag;">
                    <span slot="left">按组织架构去找</span>
                    <span slot="right"></span>
                </yd-cell-item>
            </yd-cell-group>
            <div class="tree-menu" v-show="jiekouFlag">
                <trs-tree :type="type" v-on:callbackTop="shareFun"></trs-tree>
            </div>
            <div class="contactPerson" v-show="!jiekouFlag">常用联系人</div>
            <!-- <yd-flexbox v-show="!jiekouFlag">
                <yd-flexbox-item>
                    <div class="face">
                        <img src="../../../static/images/schedule/s_1.png" />
                    </div>
                    <div class="desc">
                        <div class="name">王小明</div>
                        <div class="office">总公司／技术部／办公信息化室</div>
                    </div>
                </yd-flexbox-item>
            </yd-flexbox> -->
        </div>
    </yd-layout>
</template>
<script>
import trsTree from '../../components/util/trsTree/trsTree.vue';
import { mapGetters } from 'vuex';

export default {
    name: 'viewSchedules',
    components: {
        trsTree
    },
    data() {
        return {
            theModel: [],
            jsonData: [],
            searchFlag: false,
            name: '',
            type: false,
            bottomPopup: false,
            jiekouFlag: false,
            allPeople: [],
            orgQueryList: [],
            firstArr: []
        }
    },
    computed: {
        ...mapGetters([
            'getTitleName', 'getScheduleInfo', 'getUserInfo'
        ])
    },
    mounted() {
        this.pageRender();
        document.setTitle(this.getTitleName);
        console.log(this.getTitleName);
    },
    methods: {
        shareFun(id, e) {
            e.stopPropagation();
            let vm = this;
            // 分享
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/shareCalendar', {
                'ssotoken': vm.getUserInfo.empcode,
                'otherEmpcode': id,
                'shareUrl': vm.getScheduleInfo.shareUrl,
                'title': vm.getScheduleInfo.title,
                'message': vm.getScheduleInfo.message
            }, function(res) {
                let promise = new Promise(function(resolve, reject) {
                    vm.$dialog.toast({
                        mes: '分享成功',
                        timeout: 1500
                    });
                    resolve();
                });
                promise.then(function() {
                    vm.searchFlag = false;
                    vm.name = '';
                });
            });
        },
        search(name) {
            var key;
            var vm = this;
            var promise = new Promise(function(resolve, reject) {
                vm.orgQueryList = [];
                for (key in vm.jsonData) {
                    var r = vm.jsonData[key].text.match(new RegExp(name));
                    if (r != null) {
                        vm.orgQueryList.push(vm.jsonData[key]);
                    }
                }
            });
            promise.then();
        },
        pageRender(event) {
            var vm = this;
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/orgTree', {
                'ssotoken': vm.getUserInfo.empcode
            }, function(res) {
                var childrenArr = res.jsonData[205].children.split(';');
                vm.jsonData = res.jsonData;
                for (var i = 0; i < childrenArr.length; i++) {
                    var chnl = childrenArr[i];
                    var childrens = res.jsonData[chnl];
                    if (childrens != undefined) {
                        vm.theModel.push(childrens)
                    }
                }
            });
        }
    }
}
</script>
<style scoped>
.face {
    width: .84rem;
    height: .84rem;
    border-radius: 1000px;
    border: 1px solid #EFEEEC;
    position: absolute;
    top: .3rem;
}

.desc {
    padding: .3rem 0;
    padding-left: 1.2rem;
}

.name {
    color: #333333;
    font-size: .3rem;
}

.face img {
    width: 100%;
}

.office,
.desc {
    font-size: .24rem;
    line-height: .44rem;
}

.yd-flexbox {
    padding: 0 .24rem;
    position: relative;
}

.schedules {
    background: #ffffff;
    position: relative;
}

.schedules .searchContent {
    position: absolute;
    top: .8rem;
    left: 0;
    width: 100%;
    height: auto;
    min-height: 100%;
    background: #fff;
    z-index: 99;
}

.searchLi {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px dotted #ccc;
    padding-left: 0.8rem;
}

.contactPerson {
    background: #F0EFED;
    font-size: .26rem;
    line-height: .55rem;
    padding-left: .3rem;
}

.yd-cell-left span {
    font-size: .3rem;
}

.yd-cell-box {
    margin-bottom: 0;
}
</style>


