<template>
    <div class="tree-menu" v-show="true">
        <ul v-for="menuItem in theModel">
            <my-tree :model="menuItem" :jsonData="jsonData" :type="type" v-on:callbackTrs='callbackClick'></my-tree>
        </ul>
    </div>
</template>
<script>
import myTree from './treeMenu.vue';
export default {
    name: 'trsTree',
    components: {
        myTree
    },
    props: ['type', 'callbackTop'],
    data() {
        return {
            isLoading: true,
            jsonData: [],
            nameObj: {},
            theModel: []
        }
    },
    mounted() {
        this.pageRender();
    },
    methods: {
        callbackClick: function(obj) {
            // 触发父组件内定义的方法
            let oName = {};
            oName[obj.name] = obj.type; // 人员已删除
            this.nameObj[obj.id] = oName;
            if (this.type == 'addTrip' || this.type == 'share') {
                this.$emit('callbackTop', this.nameObj);
            } else {
                this.$emit('callbackTop', obj);
            }
        },
        pageRender() {
            const vm = this;
            // 人员列表
            vm.get(vm.conf.schedule + '/mobileInterface/orgTree', {
                'ssotoken': vm.$store.getters.getUserInfo.empcode
            }, function(res) {
                var childrenArr = res.jsonData[205].children.split(';').filter((item) => item != '');
                vm.jsonData = res.jsonData;
                for (var i = 0; i < childrenArr.length; i++) {
                    var chnl = childrenArr[i];
                    var childrens = res.jsonData[chnl];
                    if (childrens != undefined) {
                        vm.theModel.push(childrens);
                    }
                }
            });
        }
    }
}
</script>;
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


