<template>
    <li>
        <span class="cot_span" @click="toggle(model.id, model.text);">
          <i v-if="isFolder" class="icon" :class="[open ? 'folder-open': 'folder']"></i>
          <i v-if="!isFolder" class="icon file-text"></i>
          <span class="partName">{{ model.text }}</span>
        <i class="partDept" v-if="!model.children" v-text='jsonData[jsonData[model.parent].parent].text+"/"+jsonData[model.parent].text'></i>
        <i class="focus" @click.stop="addFocus(model.id);" v-if="!model.children && focusFlag">加关注</i>
        <i class="tripClass" v-bind:class="{active:tripNum%2==1}" v-if="!model.children && (type == 'addTrip' || type == 'add' || type == 'share')"></i>
        <i class="tripClass" v-bind:class="{active:model.id==getScheduleInfo.buildId&&tripNum%2==1}" v-if="!model.children && type == 'buildType'"></i>
        </span>
        <ul v-show="open" v-if="isFolder">
            <tree-menu v-for="(item,index) in datas(model.children)" :key="index" :model="item" :jsonData="jsonData" :type="type" v-on:callbackTrs='callbackClick'></tree-menu>
        </ul>
    </li>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'treeMenu',
    props: ['model', 'jsonData', 'type', 'callbackTrs'],
    data() {
        return {
            theModels: [],
            open: false,
            isFirst: true, // 第一次触发自己的调用
            shareFlag: false,
            focusFlag: false,
            tripNum: 0,
            nameObj: {},
            personArr: [],
            buildFlag: ''
        }
    },
    computed: {
        isFolder() {
            if (this.model != undefined) {
                return this.model.children;
            } else {
                return false;
            }
        },
        ...mapGetters(['getScheduleInfo'])
    },
    mounted() {
        let vm = this;
        if (!vm.type) {
            vm.focusFlag = true;
        }
        if (vm.type == 'schedule') {
            vm.focusFlag = true;
        }
        // console.log();
    },
    methods: {
        ...mapMutations([
            'setScheduleInfo'
        ]),
        callbackClick: function(obj) {
            // 触发父组件内定义的方法
            this.$emit('callbackTrs', obj);
        },
        toggle: function(id, name) {
            let vm = this;
            vm.tripNum++;
            if (vm.type == 'buildType') {
                vm.setScheduleInfo({ buildId: id });
            }
            if (vm.isFolder) {
                vm.open = !vm.open
            }
            if (!vm.model.children) {
                vm.shareFlag = true;
                if (!vm.type) {
                    // 分享
                    vm.focusFlag = true;
                    vm.get(vm.conf.schedule + '/mobileInterface/shareCalendar', {
                        'ssotoken': '01363963',
                        'otherEmpcode': id,
                        'shareUrl': vm.getScheduleInfo.shareUrl,
                        'title': vm.getScheduleInfo.title,
                        'message': vm.getScheduleInfo.message
                    }, function(res) {
                        let promise = new Promise(function(resolve, reject) {
                            vm.$dialog.toast({
                                mes: '分享成功',
                                timeout: 1500
                            });
                            resolve();
                        });
                        promise.then(function() {
                            vm.searchFlag = false;
                            vm.name = '';
                        });
                    });
                } else if (vm.type == 'add') {
                    var jsonObj = {};
                    if (vm.tripNum % 2 == 1) {
                        jsonObj = {
                            id: id,
                            name: name,
                            type: 1
                        };
                    } else {
                        jsonObj = {
                            id: id,
                            name: name,
                            type: 0
                        };
                    }
                    vm.$emit('callbackTrs', jsonObj);
                } else if (vm.type == 'addTrip' || vm.type == 'share' || vm.type == 'buildType') {
                    var jsonObj2 = {};
                    if (vm.tripNum % 2 == 1) {
                        jsonObj2 = {
                            id: id,
                            name: name,
                            type: 1
                        };
                    } else {
                        jsonObj2 = {
                            id: id,
                            name: name,
                            type: 0
                        };
                    }
                    // vm.personArr.push(jsonObj);
                    vm.$emit('callbackTrs', jsonObj2);
                } else if (vm.type) {
                    vm.viewFlag = true;
                    // 查看
                    vm.$router.push('/mySchedule/' + id + '/1/' + name);
                }
            } else {
                vm.shareFlag = false;
            }
        },
        addFocus: function(id) {
            var vm = this;
            vm.$emit('callbackTrs', id);
            // let promise = new Promise(function (resolve, reject) {
            //     alert(1)
            // });
            // promise.then(function () {
            //     alert(2)
            //     vm.searchFlag = false;
            //     vm.name = '';
            // });
        },
        datas: function(obj) {
            var theModels = [];
            var childrenArr = obj.split(';');
            for (var i = 0; i < childrenArr.length; i++) {
                var chnl = childrenArr[i];
                var childrens = this.jsonData[chnl];
                if (childrens != undefined) {
                    theModels.push(childrens)
                }
            }
            return theModels;
        }
    }
}
</script>
<style scoped>
li {
    background: #fff;
    font-size: .26rem;
    padding-left: .2rem;
    position: relative;
}

.cot_span {
    min-height: .7rem;
    display: block;
    line-height: 1;
    padding-right: .7rem;
    background: #fff;
}

.focus {
    color: #ffa21d;
    font-size: 12px;
    line-height: 1rem;
    position: absolute;
    top: 0;
    right: .3rem;
}

.tripClass {
    width: 0.4rem;
    height: 0.5rem;
    float: right;
}

.active {
    background: url(../../../../static/images/workCenter/20.png) no-repeat;
    background-position: 0 .25rem;
    background-size: .26rem;
}

.partName {
    display: inline-block;
    color: #333;
    margin-top: .22rem;
}

.partDept {
    display: block;
    padding-bottom: .2rem;
    padding-top: .15rem;
    line-height: .35rem;
}

.tripClass {
    position: absolute;
    top: .23rem;
    right: .3rem;
}

ul>li>span {
    border-bottom: 1px dashed #d0ccc8;
    line-height: 1rem;
}
</style>


